import React, { useState } from 'react';
import axios from 'axios';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
  Legend
} from 'recharts';

function App() {
  const [platform, setPlatform] = useState('codeforces');
  const [handle, setHandle] = useState('');
  const [userData, setUserData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const fetchUserData = async () => {
    if (!handle) {
      setError('Please enter a username');
      return;
    }

    setLoading(true);
    try {
      const response = await axios.get(`/api/${platform}/${handle}`); // ✅ FIXED: removed localhost
      if (response.data.status === 'OK') {
        setUserData(response.data.result);
        setError(null);
      } else {
        setError(response.data.message || 'User not found');
        setUserData(null);
      }
    } catch (err) {
      setError('Failed to fetch data');
      setUserData(null);
    }
    setLoading(false);
  };

  return (
    <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif', backgroundColor: '#f9f9f9', minHeight: '100vh' }}>
      <h2 style={{ textAlign: 'center', color: '#333' }}>Competitive Programming Tracker</h2>

      <div style={{ textAlign: 'center', marginBottom: '20px' }}>
        <select value={platform} onChange={(e) => setPlatform(e.target.value)} style={styles.select}>
          <option value="codeforces">Codeforces</option>
          <option value="leetcode">LeetCode</option>
          <option value="codechef">CodeChef</option>
        </select>

        <input
          type="text"
          value={handle}
          onChange={(e) => setHandle(e.target.value)}
          placeholder="Enter username"
          style={styles.input}
        />
        <button onClick={fetchUserData} style={styles.button}>Search</button>
      </div>

      {loading && <p style={{ textAlign: 'center' }}>Loading...</p>}
      {error && <p style={{ color: 'red', textAlign: 'center' }}>{error}</p>}

      {userData && (
        <div style={styles.cardContainer}>
          <div style={styles.card}>
            <h3 style={styles.cardTitle}>{userData.handle}</h3>
            <table style={styles.table}>
              <tbody>
                <tr>
                  <td style={styles.label}>Rating</td>
                  <td style={styles.value}>{userData.rating || 'N/A'}</td>
                </tr>
                <tr>
                  <td style={styles.label}>Max Rating</td>
                  <td style={styles.value}>{userData.maxRating || 'N/A'}</td>
                </tr>
                <tr>
                  <td style={styles.label}>Rank</td>
                  <td style={styles.value}>{userData.rank || 'N/A'}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Rating Comparison Chart */}
      {userData && (userData.rating || userData.maxRating) && (
        <div style={{ marginTop: '40px' }}>
          <h3 style={{ textAlign: 'center' }}>Rating Comparison</h3>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={[
              { name: 'Current', rating: parseInt(userData.rating) || 0 },
              { name: 'Max', rating: parseInt(userData.maxRating) || 0 }
            ]}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Bar dataKey="rating" fill="#007bff" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}

      {/* LeetCode Difficulty Chart */}
      {platform === 'leetcode' && userData && userData.totalEasy !== undefined && (
        <div style={{ marginTop: '40px' }}>
          <h3 style={{ textAlign: 'center' }}>Solved Problems by Difficulty</h3>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={[
              { difficulty: 'Easy', count: userData.totalEasy || 0 },
              { difficulty: 'Medium', count: userData.totalMedium || 0 },
              { difficulty: 'Hard', count: userData.totalHard || 0 }
            ]}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="difficulty" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="count" fill="#28a745" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}
    </div>
  );
}

const styles = {
  select: {
    padding: '8px',
    fontSize: '16px',
    marginRight: '10px'
  },
  input: {
    padding: '8px',
    fontSize: '16px',
    width: '200px',
    marginRight: '10px'
  },
  button: {
    padding: '8px 16px',
    fontSize: '16px',
    backgroundColor: '#007bff',
    color: '#fff',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer'
  },
  cardContainer: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '30px'
  },
  card: {
    backgroundColor: '#fff',
    padding: '20px 30px',
    borderRadius: '12px',
    boxShadow: '0 4px 10px rgba(0,0,0,0.1)',
    width: '350px',
    textAlign: 'center'
  },
  cardTitle: {
    margin: '0 0 20px 0',
    fontSize: '24px',
    color: '#007bff'
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse'
  },
  label: {
    textAlign: 'left',
    padding: '8px',
    fontWeight: 'bold',
    color: '#333'
  },
  value: {
    textAlign: 'right',
    padding: '8px',
    color: '#555'
  }
};

export default App;
